/* App.css */

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  background-color: #f0f0f0; /* Set your desired background color */
}

.app-container {
  text-align: center;
}

.content-container {
  background-color: grey; /* Initial color - Set your desired background color */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease; /* Smooth transition for color change */
}

.gap {
  margin-bottom: 20px;
}

/* Change color based on selection */
.selected-yes {
  background-color: #5cb85c; /* Green color for "Yes" */
}

.selected-no {
  background-color: #d9534f; /* Red color for "Probably not" */
}
